$tri1: url("../AssignmentImg/Tringle1.png");
$tri2: url("../AssignmentImg/Tringle2.png");
$Cir: url("../AssignmentImg/Circle.png");

.container-fluid {
    position: relative;
    padding: 0;
}
.Navbar-left {
    background-image: $tri1;
    background-size: cover;
    width: 1040px;
    height: 750px;
}
.Navbar-right {
    position: absolute;
    background-image: $tri2;
    background-size: cover;
    width: 855px;
    height: 550px;
    justify-content: end;
    left: 37.5rem;
    bottom: 12.6rem;
}
.Navbar-center {
    position: absolute;
    background-image: $Cir;
    background-size: cover;
    height: 329px;
    width: 381px;
}
.Navbar-content {
    color: white;
    font-weight: bold;
}
.listview {
    color: white;
    font-weight: bold;
    list-style-type: none;
    display: flex;
    // gap: 3rem;
    justify-content: space-around;
}
.Header-content {
    h1 {
        position: absolute;
        bottom: 5rem;
        padding-left: 3rem;
        color: white;
        font-weight: bold;
        font-size: xx-large;
    }
    p {
        position: absolute;
        padding-left: 3rem;
        color: white;
        bottom: 10px;
        font-size: small;
    }
}
.ofc1 {
    position: relative;
    left: 15rem;
    top: 11rem;
    z-index: 1;
}
.ofc2 {
    position: relative;
    position: relative;
    right: 14rem;
    top: 17rem;
    z-index: 2;
}

.Signup {
    color: white;
    background-image: linear-gradient(90deg, #4776e6 0%, #8e54e9 100%);
    border-radius: 10px;
    width: 10rem;
    height: 3rem;
    position: absolute;
    top: 23rem;
    left: 67px;
    font-size: medium;
    font-weight: 800;
    font-family: "Poppins", sans-serif;
}
.CircleIcon {
    min-width: 0;
    width: 20rem;
    position: absolute;
    top: 37.5rem;
    left: 17rem;
    display: flex;
    gap: 40px;
    align-items: center;
}

.CircleIcon svg {
    fill: #7a3199;
}

.microphone {
    width: 35.714px;
    height: 46.429px;
    position: absolute;
    left: 38.5rem;
    top: 34rem;
}

.CircleIconVertical {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 17rem;
    left: 42rem;
    gap: 52px;
}

.mic {
    position: absolute;
    width: 49.792px;
    height: 40.393px;
    top: 9rem;
}
.Wings {
    background-image: url("../AssignmentImg//wings.png");
    position: absolute;
    position: absolute;
    width: 25rem;
    height: 23.5rem;
    top: 20rem;
}

// Section 2



.Cards-content {
    font-weight: 600;

    font-family: "Poppins", sans-serif;
    font-size: large;
    display: inline-flex;
    padding: 20px;
    flex-direction: column;
    align-items: center;
}
.card {
    border-radius: 10px;
    border: 0.5px solid var(--secondary, #7a3199);
}
.Sub-content {
    padding: 20px;
}

.Course {
    h1 {
        font-weight: 700;
        padding-top: 40px;
    }
}

.Explore {
    color: white;
    background-image: linear-gradient(90deg, #4776e6 0%, #8e54e9 100%);
    border-radius: 10px;
    width: 15rem;
    height: 3rem;
    font-size: medium;
    font-weight: 800;
    font-family: "Poppins", sans-serif;
    outline: none;
}

//Section 3

$Line1: url("../AssignmentImg/Line\ 1.png");
$Line2: url("../AssignmentImg//Line\ 2.png");
$Line3: url("../AssignmentImg/Line\ 3.png");

.Plan {
    background-image: $Line1;
    width: 91rem;
    height: 33rem;
    gap: 10px;
    margin-bottom: 10px;
}
.Plan2 {
    background-image: $Line2;
    width: 90rem;
    position: absolute;
    height: 34rem;
    top: 2rem;
    gap: 10px;
    margin-bottom: 10px;
}
.Plan3 {
    background-image: $Line3;
    width: 90rem;
    position: absolute;
    height: 34rem;
    top: 2rem;
    gap: 10px;
    margin-bottom: 10px;
}

.Plancard{
    width: 20rem;
    margin: 0;
}
.card-body h5 {
    font-size: 25px;
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding: 10px;
}
.Plan-Price {
    color: var(--ghost, #7a3199);
    font-size: 32px;
    font-family: Inter;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    padding: 10px;
}

.span {
    font-weight: 100;
    font-size: medium;
}
.Plan-Feature {
    font-weight: 700;
}
.TrialBtn {
    text-decoration: none;
    border: 0.5px solid #7a3199;
    height: 3rem;
    width: 11rem;
    color:  #7a3199;
    border-radius: 5px;
    display: flex;
    padding: 14px 32px 12px 31px;
    justify-content: center;
    align-items: center;
}

.TrialBtn1 {
    text-decoration: none;
    border: 0.5px solid #7a3199;
    height: 3rem;
    width: 11rem;
    color:#7a3199;
    background-color: white;
    border-radius: 5px;
    display: flex;
    padding: 14px 32px 12px 31px;
    justify-content: center;
    align-items: center;
}

.TrialBtn1:hover{
    color: #7a3199;
    background-color: white;
}

.Purple{
    background-color: #7a3199;
    color: white;
}

.TrialBtn:hover{
    color: white;
    background-color: #7a3199;
}


  
  .Plan-Details-Card {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    gap: 30px; 
    margin-block-start: 11px;
    margin-inline-start: 120px;
   
  }
  